
import { computed, defineComponent } from 'vue';
import STATE from './API/state';

export default defineComponent({
  setup() {
    const { state } = STATE;

    const gogo = () => {
      state.count += 1;
    };

    const counter = computed(() => {
      return state.count;
    });

    return {
      counter,
      gogo,
    };
  },
});
