
import { defineComponent, watch, toRefs, reactive, PropType } from 'vue';
import quest, { typeQuest } from '../../assets/question';
import Qstate from '../../API/quest';
import CollestState from '../../API/state';

export default defineComponent({
  setup() {
    const { dialog } = Qstate;

    const data = reactive({
      myQuest: {} as typeQuest,
      answer: 1,
      /* 是否顯示答案 */
      showAnswer: false,
    });

    /* 取得題目 */
    const getNewQuest = () => {
      const index = Math.floor(Math.random() * quest.length);
      data.myQuest = quest[index];
    };

    const handleDialogChanged = (v: boolean) => {
      if (v) {
        data.showAnswer = false;
        data.answer = 0;
        getNewQuest();
      }
    };
    watch(dialog, handleDialogChanged);

    const { state } = CollestState;

    const handleSubmit = () => {
      const answer = data.myQuest.answer;
      const reply = data.answer;
      data.showAnswer = true;

      const replyState = {
        testIndex: 1,
        reply,
        answer,
        isPassed: answer === reply,
      };
      state.answerState.push(replyState);
      state.totalAnswerState.push(replyState);
    };

    const refData = toRefs(data);

    return { ...refData, dialog, handleSubmit };
  },
});
