
import {
  computed,
  defineComponent,
  onBeforeUnmount,
  onMounted,
  ref,
  watch,
} from 'vue';

let CLOCK_ID: any;

export default defineComponent({
  setup() {
    const timer = ref(100);

    const clock = computed(() => {
      const min = Math.floor(timer.value / 60);
      const sec = timer.value % 60;
      const setZero = (n: number | string) => (n <= 9 ? `0${n}` : n);
      return `${setZero(min)}:${setZero(sec)}`;
    });

    /** 啟用時鐘 */
    const startClock = () => {
      CLOCK_ID = setInterval(setClock, 1000);
    };

    /** 設定時鐘 */
    const setClock = () => {
      timer.value = timer.value - 1 || 0;
    };

    /** 停止時鐘 */
    const stopClock = () => {
      clearInterval(CLOCK_ID);
    };

    watch(timer, (v: number) => {
      if (v === 0) {
        alert('TIME UP!');
        stopClock();
      }
    });

    onMounted(() => {
      startClock();
    });

    onBeforeUnmount(() => {
      stopClock();
    });

    return { clock };
  },
});
